* {
    box-sizing: border-box;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  
  .book-stage {
    position: absolute;
    width: 100vw;
    height: 100vh;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
  }

  .book-container {
    width: 45vmin;
    height: 66vmin;
    margin: auto;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    -webkit-transition: all 1s;
    transition: all 1s;
    -webkit-perspective: 150vmin;
            perspective: 150vmin;
    -webkit-transform: rotateZ(3deg);
            transform: rotateZ(3deg);
  }
  
  .book-container:hover {
    left: 45vmin;
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
  
  .book-container:hover .front-cover {
    -webkit-transform: rotateY(-180deg);
            transform: rotateY(-180deg);
  }
  
  .book-insides h1 {
    font-size: 3vmin;
    line-height: 4.5vmin;
  }
  
  .book-insides p {
    margin: 0;
    line-height: 2.5vmin;
    text-indent: 3vmin;
    font-size: 1.5vmin;
  }
  
  .front-cover {
    z-index: 100;
    position: absolute;
    width: inherit;
    height: inherit;
    -webkit-transition: -webkit-transform 1s;
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
    -webkit-transform-origin: left;
            transform-origin: left;
    -webkit-transition-delay: 200ms;
            transition-delay: 200ms;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transform: rotateY(-3deg);
            transform: rotateY(-3deg);
  }
  
  .front-cover.open {
    z-index: 1;
  }
  
  .front-cover-inside {
    position: absolute;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    background: white;
    width: inherit;
    height: inherit;
  }
  
  .front-cover-outside {
    position: absolute;
    background: #dddddd;
    width: inherit;
    height: inherit;
    background-size: cover;
  }
  
  .back-cover {
    position: absolute;
    z-index: 1;
  }
  
  .back-cover-inside {
    position: absolute;
    background: green;
    width: inherit;
    height: inherit;
  }
  
  .back-cover-outside {
    position: absolute;
    background: blue;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    width: inherit;
    height: inherit;
  }
  
  .page-container {
    position: absolute;
    width: inherit;
    height: inherit;
    top: 0;
    -webkit-transition: -webkit-transform 800ms;
    transition: -webkit-transform 800ms;
    transition: transform 800ms;
    transition: transform 800ms, -webkit-transform 800ms;
    -webkit-transform-origin: left;
            transform-origin: left;
    cursor: pointer;
    overflow: hidden;
    -webkit-transform: rotateY(-1deg);
            transform: rotateY(-1deg);
  }
  
  .page-container.flipped {
    -webkit-transform: rotateY(-179deg);
            transform: rotateY(-179deg);
  }
  
  .page-container.flipping {
    -webkit-transform: rotateY(-2deg);
            transform: rotateY(-2deg);
  }
  
  .page {
    bottom: 0;
    padding: 6vmin;
    position: absolute;
    width: inherit;
    height: inherit;
    -webkit-transition: opacity 1s;
    transition: opacity 1s;
  }
  
  .back {
    z-index: 1;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    background: white;
    opacity: 0.35;
  }
  
  .front {
    z-index: 2;
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
    background: white;
    background: -webkit-gradient(linear, left top, right top, from(#fafafa), to(white));
    background: -webkit-linear-gradient(left, #fafafa, white);
    background: linear-gradient(to right, #fafafa, white);
    opacity: 0.96;
  }
  
  .flipped .front {
    z-index: 1;
    opacity: 0.35;
  }
  .flipped .back {
    z-index: 2;
    opacity: 0.96;
  }
  
  .page-container .page.back::before {
    box-sizing: border-box;
    content: "";
    z-index: 1;
    width: 45vmin;
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
    background: white;
    height: 5.8vmin;
  }
  
  .page-container .page.front::before {
    box-sizing: border-box;
    content: "";
    z-index: 1;
    width: 45vmin;
    position: absolute;
    top: 0;
    left: 0;
    height: 5.8vmin;
    background: white;
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
    background: -webkit-gradient(linear, left top, right top, from(#fafafa), to(white));
    background: -webkit-linear-gradient(left, #fafafa, white);
    background: linear-gradient(to right, #fafafa, white);
  }
  
  .page-container .page.front::after {
    z-index: 1;
    box-sizing: border-box;
    content: attr(data-page);
    width: 45vmin;
    position: absolute;
    bottom: 0;
    left: 0;
    height: 6vmin;
    font-size: 1.5vmin;
    padding-top: 1vmin;
    text-align: right;
    background: white;
    padding-right: 5.8vmin;
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
    background: -webkit-gradient(linear, left top, right top, from(#fafafa), to(white));
    background: -webkit-linear-gradient(left, #fafafa, white);
    background: linear-gradient(to right, #fafafa, white);
  }
  
  .page-container .page.back::after {
    z-index: 1;
    box-sizing: border-box;
    content: attr(data-page);
    width: 45vmin;
    position: absolute;
    bottom: 0;
    left: 0;
    background: white;
    font-size: 1.5vmin;
    height: 5.8vmin;
    text-align: left;
    padding-left: 6vmin;
    padding-top: 1vmin;
  }
  
body {
    margin: 0;
    padding: 0;
    overflow: hidden;
    height:100vh;
    background: -webkit-gradient(linear, left top, left bottom, from(#a3a3a3), to(#474747));
    background: -webkit-linear-gradient(top, #a3a3a3, #474747);
    background: linear-gradient(to bottom, #a3a3a3, #474747);
  }
 
  
